import { isFuture, format } from "date-fns";
import { it } from "date-fns/locale";

export const mapEdgesToNodes = (data) => {
  if (!data.edges) return [];
  return data.edges.map((edge) => edge.node);
};

export const filterOutDocsWithoutSlugs = ({ slug }) => {
  return (slug || {}).current;
};

export const filterOutDocsPublishedInTheFuture = ({ publishedAt }) => {
  return !isFuture(new Date(publishedAt));
};

export const showDate = (pointInTime = null, options = { minutes: false }) => {
  if (options.minutes) {
    return format(new Date(pointInTime), "kk:mm", { locale: it });
  }
  return format(new Date(pointInTime), "d, MMMM, yyyy", { locale: it });
};

export const respH = (size = 1) => {
  return `is-size-${size + 2}-mobile is-size-${
    size + 1
  }-tablet is-size-${size}-desktop`;
};
